.chat {
  width: 500px;
  border: 1px solid;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}

.youMessage {
  padding: 10px 15px;
  background-color: lightblue;
  border-radius: 5px;
}

.userMessage {
  padding: 10px 15px;
  background-color: grey;
  border-radius: 5px;
}
